body {
  box-sizing: border-box;
  --main-color: #00acdc;
  margin: 0;
  padding: 0;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header_logo {
  text-decoration: none;
  display: flex;
}

.header_logo_img {
  width: 40px;
  height: 40px;
  margin: 10px auto;
}

.nav {
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin: 0 0 0 20px;
  display: flex;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  color: #000;
  padding: 0;
  line-height: 60px;
  text-decoration: none;
  position: relative;
}

.nav__link:hover, .is-active {
  color: var(--main-color);
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  z-index: 100;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
